@import '../styles/variables';

.navbar {
  background: $color-blue;
  display: flex;
  position: relative;
  flex: 5em 0 0;
  width: 100%;
  z-index: 4;

  @media screen and (min-width: $break-lg) {
    background-color: $color-purple;
    flex-direction: column;
    padding-bottom: 2em;
    position: fixed;
    width: $sidebarWidth;
    top: 0;
    height: 100vh;
    overflow-y: auto;
  }
}

.navigation {
  position: absolute;
  width: 100%;
  top: 5em;
  right: 0;
  z-index: 1;
  background: $color-purple;
  padding: 2em 0;
  display: none;
  border-top: 2px solid $color-blue;

  &--show {
    display: block;
  }

  @media screen and (min-width: $break-sm) {
    width: calc(100% - 10em);
    border-top: none;
  }

  @media screen and (min-width: $break-lg) {
    top: 0;
    display: initial;
    position: relative;
    width: 100%;
    padding: 0;
  }

  &-list {
    padding: 0;
  }
}

.selectedSubpage {
  color: $color-purple;
  font-weight: 500;
  font-size: 1.25em;
  display: block;
  height: auto;
  text-align: left;
  padding-left: 1rem;
  flex: auto 1 0;
  cursor: pointer;

  @media screen and (min-width: $break-lg) {
    display: none;
  }
}

.oslo__logo {
  cursor: pointer;
  background: $color-purple;
  object-fit: contain;
  width: 10em;

  @media screen and (min-width: $break-lg) {
    align-self: center;
    background: none;
    margin-top: 3rem;
    margin-bottom: 2rem;
  }
}

.navigation-link {
  $p: &;

  align-items: center;
  background-color: $color-purple;
  color: rgba(white, 0.9);
  cursor: pointer;
  display: flex;
  position: relative;
  transition: all 0.3s ease-in-out;
  width: 100%;

  &--active {
    background-color: $color-blue;
    transition: all 0.3s ease-in-out;

    #{$p}__label {
      color: $color-purple;
      font-weight: 500;
    }

    label:hover {
      background: none !important;
    }

    input[type='checkbox']:checked + label {
      &::after {
        transform: scale(0);
      }
    }
  }

  &--compare {
    background-color: lighten($color-purple, 5%);
    transition: background-color 0.3s ease-in-out;
  }

  &__label {
    align-items: center;
    color: rgba(white, 0.85);
    display: flex;
    flex-grow: 1;
    height: 3em;
    letter-spacing: 0.1px;
    position: relative;

    &-compare {
      margin-bottom: 1rem;
      margin-top: 2rem;
      padding-left: 3.6rem;
    }

    // Add visual border on the left side of text on hover
    &::before {
      background: $color-purple;
      bottom: 0;
      content: '';
      display: none;
      left: -10px;
      pointer-events: none;
      position: absolute;
      top: 0;
      width: 10px;
    }
  }

  &:hover:not(&--active) label:not(.compare)::before {
    transition: all 0s;
    opacity: 0.15;
  }

  &:not(&--active) &__label:hover {
    background-color: darken($color-purple, 5%);
    color: white;

    // Show the visual border on hover
    &::before {
      background-color: darken($color-purple, 5%);
      display: block;
    }
  }
}

input[type='checkbox'] {
  display: none;

  & + label {
    cursor: pointer;
    display: inline-block;
    height: 40px;
    padding-left: 1rem;
    position: relative;
    vertical-align: middle;
    width: 50px;

    &:hover {
      background: darken($color-purple, 10%);

      &::before {
        background: rgba(white, 0.15);
        opacity: 0.8 !important;
      }
    }

    // checked inside border
    &::after {
      background-color: $color-blue;
      border-radius: 1px;
      content: '';
      height: 10px;
      left: 24px;
      position: absolute;
      top: 14px;
      transform: scale(0);
      width: 10px;
    }

    &:not(.compare) {
      &::before,
      &::after {
        transition: all 0.3s ease-in-out;
      }
    }

    // unchecked border
    &::before {
      -moz-border-radius: 1px;
      -webkit-border-radius: 1px;
      border: 1px solid white;
      border-radius: 1px;
      content: '';
      height: 18px;
      left: 20px;
      opacity: 0;
      position: absolute;
      top: 10px;
      // transition: all 0.3s ease-in-out;
      width: 18px;
    }

    &.compare::before {
      opacity: 0.35;
    }
  }

  &:checked + label {
    position: relative;
    opacity: 1;

    // checked inside border
    &::after {
      transform: scale(1);
      transition: transform 0.3s cubic-bezier(0.29, -0.01, 0.41, 1.9);
    }

    // checked border
    &::before {
      border: 1px solid $color-blue;
      opacity: 1;
      transition: transform 0.3s ease-in-out;
    }
  }
}

.navigation-drawer__buttons {
  display: flex;
  flex-direction: column;
  padding: 0 1rem;

  .navigation-drawer__button-container {
    display: flex;

    @media screen and (min-width: $break-lg) {
      align-self: flex-end;
    }

    .navigation-drawer__button {
      -moz-border-radius: 5px;
      -webkit-border-radius: 5px;
      border: 1px solid rgb(248, 198, 107);
      border-radius: 5px;
      color: rgb(248, 198, 107);
      font-weight: bold;
      margin: 0.5rem;
      padding: 0.3rem 1rem 0.3rem 1rem;
      height: 42px;
      width: 50%;
      transition: all 0.3s ease-in-out;

      @media screen and (min-width: $break-md) {
        width: auto;
      }

      @media screen and (min-width: $break-md) {
        height: auto;
      }

      &:disabled {
        opacity: 0.2;
        transition: opacity 0.3s ease-in-out;
      }

      &:hover:not([disabled]) {
        box-shadow: 0 0 0 1px #e0e0e0;
      }
    }
  }
}

.navigation-drawer__select-container {
  display: flex;
  position: relative;

  @media screen and (min-width: $break-lg) {
    align-self: flex-end;
  }

  .navigation-drawer__select {
    border: 1px solid rgb(248, 198, 107);
    color: rgb(248, 198, 107);
    font-weight: bold;
    margin: 0.5rem;
    padding: 0.5rem 1rem 0.5rem 1rem;
    position: relative;
    width: 100%;
    height: 42px;

    @media screen and (min-width: $break-lg) {
      width: 205px;
      height: auto;
    }
  }

  &:before {
    border-bottom: 2px solid rgb(248, 198, 107);
    border-left: 2px solid rgb(248, 198, 107);
    content: '';
    height: 0.5rem;
    position: absolute;
    top: calc(50% - 0.35rem);
    transform: rotate(-45deg);
    width: 0.5rem;
    right: 1.5rem;
  }
}
