

























































































































@import '../styles/colors';
@import '../styles/variables';

.oslo__navigation-topbar {
  display: flex;
  flex-direction: column;
  margin: 2rem 1rem 0 1rem;
  width: calc(100% - 2rem);
  position: relative;
  z-index: 3;
  padding: 1rem 0;

  @media screen and (min-width: $break-lg) {
    padding: 2rem;
    width: 100%;
    background-color: white;
    box-shadow: 0 1px 2px $color-grey-300;
    margin: 0;
  }

  &-button {
    display: none;
    flex-direction: row;

    @media screen and (min-width: $break-lg) {
      display: flex;
    }
  }
}

.oslo__topbar {
  color: $color-purple;
  font-weight: bold;
  margin-bottom: 2rem;

  &-text {
    letter-spacing: 0.8px;
    margin-left: 1rem;
    margin-top: 0.3rem;
    text-transform: uppercase;
  }
}

.label {
  font-size: 16px;
  transition: all 0.3s ease-in-out;

  &--active {
    font-size: 16px;
    left: 0;
    position: absolute;
    right: auto;
    transform: translateY(-18px) translateX(-12px) scale(0.75);
    transition: all 0.3s ease-in-out;
  }
}

.navigation-topbar {
  width: 100%;
  max-width: 768px;
  position: relative;

  &--hidden {
    @media screen and (max-width: $break-lg) {
      display: none;
    }
  }

  &__select {
    color: rgb(141, 141, 160);
    display: flex;
    font-size: 24px;
    font-weight: bold;
    justify-content: space-between;
    letter-spacing: 0.7px;
    padding: 0;
    position: relative;
    text-transform: uppercase;
    width: 100%;

    &:before {
      border: 0.5px solid black;
      bottom: -1px;
      content: '';
      left: 0;
      position: absolute;
      transition: 0.3s cubic-bezier(0.25, 0.8, 0.5, 1);
      width: 100%;
    }

    &:focus {
      outline: none;
    }

    &:focus:after {
      transform: scaleX(1);
    }

    &:after {
      border: 1px solid $color-purple;
      bottom: -1px;
      content: '';
      position: absolute;
      transform: scaleX(0);
      transition: 0.3s cubic-bezier(0.25, 0.8, 0.5, 1);
      width: 100%;
    }
  }

  &__dropdown {
    background-color: white;
    border: 1px solid $color-grey-100;
    box-shadow: 0 2px 3px 0 rgba(0, 0, 0, 0.65);
    border-bottom-left-radius: 2px;
    border-bottom-right-radius: 2px;
    width: 100%;
    display: flex;
    flex-flow: row wrap;
    position: absolute;
    z-index: 1;
    padding: 0.5rem;

    &-column {
      display: flex 0 0;
      flex-direction: column;
      flex: 33.333%;
      min-width: 200px;
      padding: 0.5rem;

      &--heading {
        font-weight: bold;
        margin-bottom: 1rem;
        margin-top: 1rem;

        @media screen and (min-width: $break-lg) {
          margin-top: 0;
        }
      }
    }

    &-items {
      display: flex;
      flex-direction: column;
    }

    &-item {
      background-color: $color-grey-50;
      border-radius: 4px;
      color: $color-purple;
      // height: 34px;
      margin-bottom: 0.5rem;
      padding: 1rem 0.75rem;

      @media screen and (min-width: $break-lg) {
        margin-bottom: 0.1rem;
        padding: 0.5rem;
      }

      &:hover:not(&--active) {
        background-color: $color-grey-100;
        color: black;
      }

      &--active {
        background-color: $color-blue;
      }
    }
  }
}
