











































































































































@import './../styles/colors';

.card-container {
  padding: 0.5em;
  flex: 50% 1 10;
  min-width: 300px;
  overflow: hidden;

  @media screen and (min-width: 950px) {
    min-width: 400px;
    max-width: 50%;
  }

  &.large {
    max-width: 100%;
    flex-basis: 100%;
  }
}

.card {
  background: white;
  border: 1px solid $color-grey-100;
  border-radius: 3px;
  box-shadow: 0 1px 2px $color-grey-600;
  width: 100%;

  &__header {
    width: 100%;
    display: flex;
  }
}

/* Style the tabs */
.tabs {
  background-color: $color-grey-50;
  border-top-left-radius: 4px;
  border-top-right-radius: 4px;
  display: flex;
  width: calc(100% - 3em);
  overflow-x: auto;
  overflow-y: hidden;

  div:first-of-type a:first-child {
    border-top-left-radius: 3px;
  }

  &__button {
    background-color: $color-grey-50;
    color: rgba($color-purple, 0.8);
    cursor: pointer;
    float: left;
    font-weight: bold;
    transition: background-color 0.2s;
    padding: 1em 1.5em;

    &.active {
      background-color: #fff;
      box-shadow: 0 0 0 1px #e0e0e0;
      cursor: default;
      position: relative;
      color: black;

      &:after {
        background-color: white;
        bottom: -2px;
        content: '';
        height: 2px;
        left: 0;
        position: absolute;
        right: 0;
      }
    }

    /* Change background color of tabs on hover */
    &:hover:not(.active) {
      background-color: #efefef;
      box-shadow: 0 0 0 1px #e0e0e0;
      position: relative;
    }
  }
}

.context-menu {
  position: relative;
  margin-left: auto;
  z-index: 2;

  &__button {
    background-color: $color-light-blue-2;
    border-top-right-radius: 3px;
    height: 100%;
    width: 4rem;
  }

  &__dropdown {
    background-color: rgb(178, 210, 216);
    box-shadow: 0 2px 2px 0 $color-grey-200;
    position: absolute;
    right: 0;
    width: 200px;
    z-index: 1;

    &-item {
      color: $color-purple;
      display: flex;
      flex-direction: row;
      font-weight: bold;
      padding: 1rem 0.75rem;
      width: 100%;
      cursor: pointer;

      &:hover {
        background-color: lighten($color-light-blue-2, 5%);
      }

      &-icon {
        color: rgb(41, 40, 88);
        font-size: 16px;
      }

      span {
        margin-left: 0.5rem;
      }
    }
  }
}
